button{
    width: 250px;
    height: 80px;
    font-size: 35px;
    font-family: Roboto, sans-serif;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}
button:hover{
    background-color: #2c53ad;
}
.boton-clic{
    background-color: #79DAE8;
}
.boton-reiniciar{
    background-color: #0AA1DD;
}